'use strict';

const React = require('react');
const { Component, Fragment } = React

const PropTypes = require('prop-types');

const BannerLink = ({url, children}) => (
	<a target='_blank' href={url} className='out-of-date-banner--link'>
		{children}
	</a>
)

const OutOfDateBanner = ({ links }) => (
	<div className='out-of-date-banner'>
				
		<svg className='out-of-date-banner--warning-icon' width="16px" height="16px" viewBox="0 0 16 16" version="1.1">
				<title>Glyphs / Warning</title>
				<g id="Glyphs-/-Warning" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
					<path d="M9.30723029,0.76487417 L15.7424591,12.2052809 C16.1486048,12.9273177 15.8925244,13.8418903 15.1704876,14.248036 C14.9459652,14.3743299 14.692701,14.4406727 14.4350958,14.4406727 L1.56463821,14.4406727 C0.736211087,14.4406727 0.0646382121,13.7690999 0.0646382121,12.9406727 C0.0646382121,12.6830675 0.130981021,12.4298033 0.257274906,12.2052809 L6.69250368,0.76487417 C7.09864939,0.0428373535 8.01322203,-0.213242986 8.73525884,0.192902723 C8.97469558,0.327585886 9.17254713,0.525437435 9.30723029,0.76487417 Z M6.74986699,4.36840711 L6.99986699,9.36840711 L8.99986699,9.36840711 L9.24986699,4.36840711 L6.74986699,4.36840711 Z M7.99986699,12.8684071 C8.69022292,12.8684071 9.24986699,12.308763 9.24986699,11.6184071 C9.24986699,10.9280512 8.69022292,10.3684071 7.99986699,10.3684071 C7.30951105,10.3684071 6.74986699,10.9280512 6.74986699,11.6184071 C6.74986699,12.308763 7.30951105,12.8684071 7.99986699,12.8684071 Z" fill="#86681D"></path>
				</g>
		</svg>

		<div>
			<p className='out-of-date-banner--text'>
				<b>The information on this page is out of date.</b> While we're updating these pages, please consult the "Design Systems" page on the wiki, or ask your friendly neighborhood design systems team if you have any questions.
			</p>
			
			{links && (
				<div>
					<br />

					{links.map((link, i) => (
						<div key={i} className='out-of-date-banner--resource'>
							{link.url ? (
								<BannerLink url={link.url}>
									{link.title}
								</BannerLink>
							) : link.title}

							{link.sources && link.sources.map((source, j) => (
								<span key={j}>
									&nbsp;&nbsp;

									<BannerLink url={source.url}>
										({source.title})
									</BannerLink>
								</span>
							))}
						</div>
					))}
				</div>
			)}
		</div>
	</div>
);

OutOfDateBanner.propTypes = {
	links: PropTypes.arrayOf(PropTypes.shape({
		title: PropTypes.string.required,
		url: PropTypes.string,
		sources: PropTypes.arrayOf(PropTypes.shape({
			title: PropTypes.string.required,
			url: PropTypes.string.required,
		}))
	}))
};

export default OutOfDateBanner;



